.hero,
.topContainer,
.headingContainer,
.cardContainer,
.card {
    display: flex;
    flex-flow: column nowrap;

}

.hero {
    gap: 3.125rem;
    font-family: 'Open Sans', sans-serif;
    padding-bottom: 3.125rem;
}

.topContainer {
    flex-flow: column-reverse nowrap;
    align-items: center;

    img {
        width: 80%;
    }
}

.headingContainer {
    gap: 1rem;

    p {
        margin-top: -0.5rem;
    }
}

h1, h4 {
    font-weight: 700;
    line-height: 160%;
    color: #060C22;
}

h1 {
    font-size: 2.125rem;
}

h4 {
    font-size: 1.125rem;
}

p {
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 120%;
    color: #060C22;
}

.btn {
    padding: 0.875rem;
    background: #714DD9;
    border-radius: 0.5rem;
    color: white;
    width: fit-content;
    cursor: pointer;
    border: none;
}

.cardContainer {
    align-items: center;
    justify-content: center;
    gap: 3.125rem;
}

.card {
    gap: 0.625rem;
    padding: 1.5rem 1rem;
    width: 17.125rem;
    background: #FFFFFF;
    border-radius: 1rem;

    img {
        margin-bottom: 1.5rem;
        width: 4.5rem;
        height: 4.5rem;
    }

}

@media screen and (min-width: 48rem) {
    .hero {
        gap: 6.25rem;
    }

    .topContainer {
        flex-flow: row nowrap;
        align-items: flex-start;
        gap: 3.125rem;
        width: 100%;

        img {
            width: 50%;
            margin-top: -2rem;
        }
    }

    .headingContainer {
        width: 60%;
        margin-top: 3.125rem;
    }

    h1 {
        font-size: 3.8125rem;
    }

    h4 {
        font-size: 1.5625rem;
    }

    p {
        font-size: 1rem;
    }

    .cardContainer {
        flex-flow: row nowrap;
        gap: 4.625rem;
        padding-top: 6.25rem;
    }

    .card:nth-child(2){
        transform: translateY(-7rem);
    }
}